<template>
  <div class="healtheducation"> 
  	<headline />
  	<div class="healtheducation-head" style="font-size: .36rem;color: #3C9BFF;">
  		
  	</div>
  	<div class="healtheducation-content">
  		<ul class="video-list">
  			<li class="video-item" @click.stop="playing('http://fc-zb.ufile.ucloud.com.cn/chest%2F20181212%2Fxinzangkangfu.mp4')">
  				<div class="video-item-banner"><img src="https://www.fc18.com.cn/ymmopenapi/sgw/content/fileResource/412037664"/></div>
  				<div class="video-item-icon"><img src="../../../assets/stop_icon.png"/></div>
  				<div class="video-item-text"><p></p></div>
  			</li>
  			<li class="video-item" @click.stop="playing('http://fc-zb.ufile.ucloud.com.cn/chest%2F20181212%2Fxinzangzhijia.mp4')">
  				<div class="video-item-banner"><img src="http://ymmsit.verify.fc18.com.cn/att/ymm/news/_2018121202.png"/></div>
  				<div class="video-item-icon"><img src="../../../assets/stop_icon.png"/></div>
  				<div class="video-item-text"><p></p></div>
  			</li>
  		</ul>
  		<ul class="article-list">
  			<li class="article-item">
  				<div class="article-item-left">
  					<img src="http://ymmsit.verify.fc18.com.cn/att/ymm/news/_2018121206.png"/>
  				</div><div class="article-item-right">
  					<h5>101</h5>
  					<p>2019219ACPAnnals of Internal Medicine</p>
  				</div>
  			</li>
  			<li class="article-item">
  				<div class="article-item-left">
  					<img src="http://ymmsit.verify.fc18.com.cn/att/ymm/news/20181204142800.png"/>
  				</div><div class="article-item-right">
  					<h5></h5>
  					<p></p>
  				</div>
  			</li>
  		</ul>
  	</div>
  	<div class="task_info" id="mod_player" v-show="showVideo"></div>
    <img src="@/assets/close.png" class="close_video" v-show="showVideo" @click="stopVideo">
  	<div class='QR-Code' @click.stop="showwincode">
  		<img src="../../../assets/QR-Code.png" />
  	</div>
  	<div class="win-code" v-if="iswincode">
  		<div class="win-code-content">
  			<img src="../../../assets/erweima.png" class="win-code-banner"/>
  			<img src="../../../assets/close.png" class="win-code-icon" @click.stop="closewincode"/>
  			<p style="font-size: .36rem;margin-top: 15px;"> </p>
  		</div>
  	</div>
  </div>
</template>
<script>
import $ from 'jquery'
import 'utils/play.js'
import {getKnowledgeTag,cancelAttention} from 'API/requst'
import headline from 'components/headline/headline.vue'
import { getURLParameter,dateFormat } from 'utils/util'
var livePlayer = new ump.Player();
export default {

  name: 'healtheducation',
  components: {
  },
  data () {
    return {
     showVideo:false,
     iswincode:false,
    }
  },
  methods:{
  	showwincode:function(){
  		var vm = this;
  		vm.iswincode = true;
  	},
  	closewincode:function(){
  		var vm = this;
  		vm.iswincode = false;
  	},
  	playing(index){
      this.showVideo=true;
      setTimeout(function(){
      //
			var params = {
				autostart:true, //
				file:index,// url
				markname:"" //
			};
			livePlayer.onInited = function (info) {
	        ump.log('')
	        for(var o in info){
	            ump.log(o + "=" + info[o]);
	        }
	        /************flash************/
	        //
	        //player.stop();
	
	        //player.onState
	        //var state = player.getState();
	    };
	
	    //
	    livePlayer.onState = function(info) {
	        if(!info || (info && typeof info.type== undefined)){
	            return;
	        }
	
	        ump.log(""+info.type);
	
	        switch (info.type){
	            //
	            case "loading":
	                break;
	            //loading
	            case "playing":
	                break;
	            //stop
	            case "stop":
	                break;
	        }
	    };
	
	    //
	    livePlayer.onError = function(info) {
	        if(!info){
	            return;
	        }
	
	        if(info.type){
	            //
	            ump.log(""+info.type);
	        }
	
	        if(info.desc){
	            //
	            ump.log(""+info.desc);
	        }
	    };
	
	    //html5
	    livePlayer.onUserAction = function(info) {
	        if(!info || (info && typeof info.type== undefined)){
	            return;
	        }
	
	        ump.log(":"+info.type);
	
	        switch (info.type){
	            //
	            case "playBtnClick":
	                break;
	            //
	            case "pauseBtnClick":
	                break;
	            //
	            case "fullscreen":
	                break;
	            //
	            case "normalscreen":
	                break;
	            //
	            case "muteBtnClick":
	                break;
	            //
	            case "unmuteBtnClick":
	                break;
	            case "volumeChange":
	                ump.log(""+info.value);
	                break;
	            default:
	                ump.log("Unkown user action!");
	        }
	    };
			livePlayer.create({
				width : '100%',
		    height : '100%',
		    modId : "mod_player",
        params : params
			})
      //livePlayer.setFullscreen(true)
      },200)
    },
    stopVideo(){
       //var livePlayer = new ump.Player();
      // var params = {
      //   autostart:false, //
      //   file:"http://fczb.ufile.ucloud.com.cn/chest%2F20190129%2Faa49379eb0754be9f990fb85bb3644bc.mp4",// url
      //   markname:"" //
      // };
      // livePlayer.create({
      //   width : '100%',
      //   height : '100%',
      //   modId : "mod_player",
      //   params : params
      // })
      livePlayer.stop()
      this.showVideo = false
    }
  },
  components:{
    headline
  },
  created () {
  	var vm = this;
  },
  mounted(){

  }
}
</script>
<style>
	.healtheducation{
		padding-left: 200px;
		font-size: .3rem;
	}
	.healtheducation .head{
		height: 1.7rem !important;
	}
	.healtheducation .head .back{
		top: 50%;
    transform: translateY(-50%);
	}
	.healtheducation .gray{
		color: #999999;
	}
	.healtheducation-head{
		border-bottom: 1px solid #eee;
    padding-left: 150px;
		display: flex;
    height: 1.7rem;
    align-items: center;
	}
	.healtheducation-head span{
		margin-left: 10px;
	}
	.healtheducation-head>div {
    display: inline-block;
    width: 33.33%;
    vertical-align: middle;
	}
	.head-name img{
		width: .9rem;
    height: .9rem;
    border-radius: 50%;
    margin-right: .2rem;
    vertical-align: middle;
	}
	.head-name>div{
		display: inline-block;
		vertical-align: middle;
	}
	.healtheducation-content{
		margin-top: 30px;
		padding-left: 30px;
	}
	.video-list,.video-item,.video-item-banner,.video-item-banner>img{
		height: 32vh;
	}
	.video-item{
		display: inline-block;
    width: 38%;
    position: relative;
    margin-right: 20px;
	}
	.video-item-banner img{
		width: 100%;
	}
	.video-item-icon{
		position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
	}
	.video-item-icon img{
		height: 1.1rem;
    width: auto;
	}
	.video-item-text{
		position: absolute;
    bottom: 0;
    left: 0;
    color: #fff;
    background: rgba(0,0,0,0.5);
    width: 100%;
    padding: 10px 0 10px 0px;
    text-indent: 10px;
	}
	.article-item{
		padding: 15px 0;
    border-bottom: 1px solid #eee;
	}
	.article-item>div {
	    display: inline-block;
	    vertical-align: middle;
	}
	.article-item-left img{
		width: 1.7rem;
    height: 1.7rem;
	}
	.article-item-right{
    max-width: 85%;
	}
	.article-item-right h5{
		font-size: .3rem;
		padding-left: 15px;
	}
	.article-item-right p{
		margin-top: 20px;
    color: #9999;
    padding-left: 15px;
	}
	.QR-Code{
    position: fixed;
    top:0%;
    right:20px;
	}
	.QR-Code img{
		width: auto;
		height: 1.7rem;
	}
	/**/
	#mod_player{
		position: fixed;
    top: 0px;
    width: calc(100% - 200px);
    height: 100%;
    left: 200px;
    z-index: 10;
	}
	#mod_player video{
    object-fit: fill;
	}
	.close_video{
		position: fixed;
    top: 20px;
    right: 10px;
    z-index: 11;
	}
	.win-code{
		width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    top: 0;
    left: 0;
    position: fixed;
    z-index: 10;
	}
	.win-code-content{
		width: 45%;
    left: 30%;
    background: #fff;
    position: absolute;
    top: 50%;
    text-align: center;
    border-radius: 10px;
    transform: translateY(-50%);
    padding: 20px 0;
	}
	.win-code-banner{
		width: 50%;
    height: auto;
	}
	.win-code-icon{
		position: absolute;
    top: 20px;
    right: 20px;
	}
</style>



